define('extensions/adapt-commentsList/js/adapt-commentView',['require','jquery','coreJS/adapt','backbone','./utils'],function(require) {

	var $ = require('jquery');
	var Adapt = require('coreJS/adapt');
	var Backbone = require('backbone');
	var Utils = require('./utils');

	var CommentView = Backbone.View.extend({
		className: 'comments-list-extension-widget-comment',
		template: 'commentView',
		editCommentElement: false,
		renderTimer: false,

		data: {},

		initialize: function() {
			this.data = this.model.toJSON();
			var global = Adapt.course
									.get('_globals')
									._extensions
									._commentsList;
			this.data.editable = global.editable;
			this.data.togglable = global.togglable;
			this.data.owner = this.data.author_id === this.data.current_user_id;

			this.setupEvents();
      this.render();
    },

		render: function() {
      var template = Handlebars.templates[this.template];

      this.$el.html(template(this.data));
			this.editCommentElement = this.$('#comments-list-comment-textarea-'+this.data.id);

			return this;
    },

		setupEvents: function() {
			this.setupDelegetedEvents();

      this.listenTo(Adapt, 'remove', this.remove);
			this.listenTo(this.model, 'change', this.render);
		},

		setupDelegetedEvents: function() {
			var customEventsListener = {};

			customEventsListener[
				'dblclick #comments-list-extension-widget-item-' +
				this.data.id
			] = 'editComment';

			customEventsListener[
				'click #comments-list-extension-item-button-edit-' +
				this.data.id
			] = 'editComment';

			customEventsListener[
				'click #comments-list-extension-item-button-delete-' +
				this.data.id
			] = 'deleteComment';

			customEventsListener[
				'click #comments-list-extension-item-button-cancel-deletion-' +
				this.data.id
			] = 'cancelDeletionComment';


			this.delegateEvents(
				customEventsListener
			);
		},

		checkIfTargetIsTheSameTextarea: function(event) {
			return $('#'+event.target.id).is('textarea#comments-list-comment-textarea-'+this.data.id);
		},

		readOnly: function() {
			return this.data.togglable === false;
		},

		preEditCommentCheck: function(event) {
			return (event && this.checkIfTargetIsTheSameTextarea(event)) || this.readOnly();
		},

		// TODO: refactor editComment, too complex...
		editComment: function(event, save) {
			var base = this;
			save = save || true;

			if (this.preEditCommentCheck(event)) {
				return;
			}

			if (base.renderTimer) {
				clearTimeout(base.renderTimer);
			}

			if (this.data.owner) {
				this.data.edition_mode = !this.data.edition_mode;

				if (!this.data.edition_mode) {
					var displayedMessage = this.getCommentContent();

					if (save && this.data.comment !== displayedMessage) {
							var newDate = new Date().getTime();
							this.data.comment = displayedMessage;
							this.data.changed = true;
							this.data.timestamp = newDate;

							this.model.set('changed', true);
							this.model.set('comment', this.data.comment);
							this.model.set('timestamp', newDate);

							this.trigger('commentsList' + this.data.block_id + ':edit', this.model.toJSON());
					}

					Adapt.trigger('commentsList::' + this.data.block_id + '/activate-new-comment');
					this.render();

				} else {

					Adapt.trigger('commentsList::' + this.data.block_id + '/disable-new-comment');
					this.model.set('comment', this.getCommentContent(true));
					this.render();

					this.editCommentElement
						.val(this.model.get('comment'))
						.focus()
						.focusout(function() {
							base.renderTimer = setTimeout(function() {
								base.data.edition_mode = false;
								Adapt.trigger('commentsList::' + base.data.block_id + '/activate-new-comment');
								base.render();
							}, 150);
						})
						.trigger('focus')
						.keyup(function(event){
							if (event.keyCode === 13  && !event.shiftKey) {
								event.preventDefault();
								base.editComment();
							} else if (event.keyCode == 27) {
								event.preventDefault();
								base.data.edition_mode = false;
								Adapt.trigger('commentsList::' + base.data.block_id + '/activate-new-comment');
								base.render();
							}
						});
				}
			}
		},

		getCommentContent: function(editMode) {

			editMode = editMode || false;

			return (editMode) ?
				Utils.revertHtmlChars(this.model.get('comment')).replace(/<br\s*\/?>/g, '\n') :
				Utils.escapeHtml(this.editCommentElement.val()).replace(/\n/g, '<br/>');
		},

		deleteComment: function() {
			this.trigger('commentsList' + this.data.block_id + ':delete', this.model.attributes);
		},

		cancelDeletionComment: function() {
			this.trigger('commentsList' + this.data.block_id + ':cancelDeletion', this.model.attributes);
		},
	});

	return CommentView;

});

