define('extensions/adapt-annotation/js/adapt-annotation/annotation',['require','backbone','../utils/utils'],function(require) {

	var Backbone = require('backbone');
	var Utils = require('../utils/utils');

	var Annotation = Backbone.Model.extend({
		defaults: {
			id: "",
			coordinates: {
				top: 0,
				left: 0
			},
			width: 0,
			height: 0,
			author_id: '',
			author_name: '',
			comment: "",
			timestamp: new Date().getTime(),
			editable: false,
			ownAnnotation: false,
			deletion_status: false,
			edition_mode: false,
			trigger: false,
			changed: false
		},

		initialize: function() {
			this.parseTimestamp();

			this.on('change:timestamp', this.parseTimestamp, this);
		},

		parseTimestamp: function() {
			var parsedDate = new Date(parseInt(this.get('timestamp'), 10));
			var formatedDate = Utils.formatDate(parsedDate);

			this.set('date', formatedDate);
		},

	});

	return Annotation;
});

