define('extensions/adapt-commentsList/js/adapt-commentsListView',['require','underscore','coreJS/adapt','backbone','jquery','jqueryUI','./adapt-commentView','./adapt-commentModel','./adapt-commentsCollection'],function(require) {

	var _ = require('underscore');
	var Adapt = require('coreJS/adapt');
	var Backbone = require('backbone');
	var $ = require('jquery');
	var JqueryUI = require('jqueryUI'); //eslint-disable-line
	var CommentView = require('./adapt-commentView');
	var Comment = require('./adapt-commentModel');
	var Comments = require('./adapt-commentsCollection');

	var CommentsListExtensionView = Backbone.View.extend({
		events: {
			"click .comments-list-extension-button": "onSpecButtonClicked",
		},
		className: 'comments-list-extension',
		initialStateChanged: false,
		commentsList: undefined,
		edition: false,
		newComment: false,
		buttonsActive: true,
		commentsFeed: false,
		block_id: "",
		commentsListContainer: false,
		toggleButton: false,
		cancelButton: false,
		saveButton: false,
		templateParams: {
			block_id: "",
			editable: "",
			togglable: "",
			emptyCollection: ""
		},
		params: {
			is_enabled: false,
			editable: true,
			current_user_id: "",
			current_user_name: "",
			togglable: true,
			draggable: true,
			openButtonText: "open comments list button",
			closeButtonText: "close comments list button"
		},

		initialize: function() {

			this.block_id = this.model.get('_id');
			this.params = Adapt.course.get('_globals')._extensions._commentsList;

			if (this.params.is_enabled) {
				this.setupCommentsListCollection();
				this.setupViewParams();
				this.setupEvents();
				this.registerToExternalListeners();
				this.render();
			}
		},

		render: function() {
			if (this.params.is_enabled) {
				this.renderTemplate();
				this.saveDOMRefs();
				this.addAllCommentsToView();

				_.defer(_.bind(this.postRender, this));
			}

			return this;
		},

		postRender: function() {
			this._setupSaveOnEnterKeyPress();
			this.setLayout();
			this.makeListDraggable();
			this.setupPostRenderEvent();
			this.processExtensionIcon();
		},

		setupEvents: function() {
			this.setupDelegetedEvents();
			this.setupExternalEvents();
			this.setupLocalEvents();
		},

		setupCommentsListCollection: function() {
			var blockJSON = this.model.toJSON();

			if (blockJSON._commentsList._comments.length > 0) {
				this.commentsList = new Comments(blockJSON._commentsList._comments);
			} else {
				this.commentsList = new Comments();
			}
		},

		setupViewParams: function() {
			this.templateParams = {
				block_id: this.block_id,
				editable: this.params.editable,
				togglable: this.params.togglable,
				emptyCollection: this.checkCollectionSizeStat()
			};
		},

		checkCollectionSizeStat: function() {
			return this.commentsList.size() <= 0;
		},

		renderTemplate: function() {
			var template = Handlebars.templates['commentsList'];

			this.$el.html(template(this.templateParams)).appendTo($('.' + this.model.get('_id')));
		},

		saveDOMRefs: function() {
			this.newComment = this.$('#comments-list-comment-textarea-'+this.block_id);
			this.commentsFeed = this.$('#comments-list-extension-widget-feed-'+this.block_id);
			this.commentsListContainer = this.$('.comments-list-extension-widget');
			this.toggleButton = this.$('.comments-list-extension-button');
			this.cancelButton = this.$('#comments-list-extension-button-cancel-'+this.block_id);
			this.saveButton = $('#comments-list-extension-button-save-'+this.block_id);
		},

		setupPostRenderEvent: function() {
			this.listenTo(Adapt, 'remove', this.remove);
			this.listenTo(Adapt, 'comments-list-extension-widget:open', this.checkIfShouldClose);
			this.setupScreenClickEvents();
		},

		makeListDraggable: function() {
			if (this.params.draggable) {
				this.commentsListContainer.draggable({
					addClasses: false,
					containment : $('.'+this.block_id)
				});
			}
		},

		addCommentToView: function(comment) {
			var base = this;

			comment.attributes.block_id = this.block_id;
			comment.attributes.current_user_id = this.params.current_user_id;

			var commentView = new CommentView({ model: comment});

			this.listenTo(commentView, 'commentsList' + this.block_id + ':edit', $.proxy(base.editComment, base));
			this.listenTo(commentView, 'commentsList' + this.block_id + ':delete', $.proxy(base.requestCommentDeletion, base));
			this.listenTo(commentView, 'commentsList' + this.block_id + ':cancelDeletion', $.proxy(base.cancelDeletionComment, base));

			this.commentsFeed.prepend(commentView.render().el);
			this.processExtensionIcon();
		},

		addAllCommentsToView: function() {
			this.commentsFeed.empty();
			this.commentsList.sort();
			this.commentsList.each(this.addCommentToView, this);
			this.processExtensionIcon();
		},

		activateNewComment: function() {
			if (!this.buttonsActive) {
				this.cancelButton
					.fadeTo("fast", 1)
					.prop('disabled', false)
					.css({
						cursor: 'pointer'
					});
				this.saveButton
					.fadeTo("fast", 1)
					.prop('disabled', false)
					.css({
						cursor: 'pointer'
					});
				this.buttonsActive = true;
			}
		},

		disableNewComment: function() {
			if (this.buttonsActive) {
				this.cancelButton
					.fadeTo("fast", 0.3)
					.prop('disabled', true)
					.css({
						cursor: 'default'
					});
				this.saveButton
					.fadeTo("fast", 0.3)
					.prop('disabled', true)
					.css({
						cursor: 'default'
					});
				this.buttonsActive = false;
			}
		},

		_setupSaveOnEnterKeyPress: function() {
			var base = this;

			this.newComment.keyup(function(event){
				if (event.keyCode === 13 && !event.shiftKey){
					event.preventDefault();
					base.addNewComment();
				}
			});
		},

		cancelNewComment: function() {
			if (this.buttonsActive) {
				this.newComment.val('');
			}
		},

		addNewComment: function() {
			if (this._getCommentContent() !== "" && this.buttonsActive) {
				this.commentsList.add(this.generateComment());
				this.cancelNewComment();
				this.commentsFeed.animate({ scrollTop: 0 }, "fast");
				this._reportExtensionStateChanged();
			}
		},

		processExtensionIcon: function() {
			this.templateParams.emptyCollection = this.checkCollectionSizeStat();
			if (this.templateParams.emptyCollection) {
				this.toggleButton.addClass('comments-list-extension-button-empty');
			} else {
				this.toggleButton.removeClass('comments-list-extension-button-empty');
			}
		},

		generateComment: function() {
			return new Comment({
				"id": this._GUID(),
				"block_id": this.block_id,
				"author_id": this.params.current_user_id,
				"author_name": this.params.current_user_name,
				"comment": this._getCommentContent(),
				"date": "",
				"timestamp": new Date().getTime(),
				"editable": true,
				"deletion_status": false
			});
		},

		editComment: function(_comment) {
			if (this._commentBelongsToUser(_comment)) {
				var comment = this.commentsList.get(_comment.id);

				comment.set(this.buildCommentProperties(_comment));
				this.commentsList.set(comment, { remove: false });
				this.commentsList.trigger('reset');
				this._reportExtensionStateChanged();
			}
		},

		buildCommentProperties: function(_comment) {
			return {
				'comment': _comment.comment,
				'date': _comment.date,
				'timestamp': _comment.timestamp,
				'changed': _comment.changed,
			};
		},

		deleteComment: function(_comment) {
			if (this._commentBelongsToUser(_comment)) {
				var comment = this.commentsList.get(_comment.id);
				if (comment) {
					this.activateNewComment();
					this.commentsList.remove(comment);
					this.commentsList.trigger('reset');
					this._reportExtensionStateChanged();
				}
			}
		},

		_commentBelongsToUser: function(comment) {
			return comment.author_id === this.params.current_user_id;
		},

		validateDeletionRequest: function(data) {
			return (
				data &&
				typeof data === "object" &&
				data.hasOwnProperty('id') &&
				data.id === this.id
			);
		},

		setupLocalEvents: function() {
			this.listenTo(this.commentsList, 'add', this.addCommentToView);
			this.listenTo(this.commentsList, 'reset', this.addAllCommentsToView);
			this.listenTo(Adapt, 'commentsList::' + this.block_id + '/activate-new-comment', this.activateNewComment);
			this.listenTo(Adapt, 'commentsList::' + this.block_id + '/disable-new-comment', this.disableNewComment);
		},

		setupScreenClickEvents: function() {
			var base = this;

			if (this.params.togglable) {
				Adapt.on('adapt::commentsList/toggle', function() {
					base.hideCommentsList();
					if (!base.buttonsActive) {
						Adapt.trigger('commentsList::' + base.block_id + '/activate-new-comment');
					}
				});
			}
		},

		setupDelegetedEvents: function() {

			var customEventsListener = {};

			customEventsListener[
				'click #comments-list-extension-button-cancel-' +
				this.block_id
			] = 'cancelNewComment';

			customEventsListener[
				'click #comments-list-extension-button-save-' +
				this.block_id
			] = 'addNewComment';

			this.delegateEvents(
				_.extend(
					_.clone(this.events),
					customEventsListener
				)
			);
		},

		registerToExternalListeners: function() {
			var base = this;

			$(window.document).trigger('adapt::register', [{
				"extension-name": 'commentsList',
				"extension-id": base.block_id,
			}]);
		},

		generateResponseObject: function() {
			return {
				"extension-name": 'commentsList',
				"extension-id": this.block_id,
				response: {
					block_id: this.block_id
				}
			};
		},

		getResponseObject: function(type) {
			var responseObject = this.generateResponseObject();
			switch(type) {
				case 'ExtensionState':
					responseObject.response['state'] = this.initialStateChanged;
				break;
				case 'Collection':
					responseObject.response['collection'] = this.commentsList.toJSON();
				break;
			}
			return responseObject;
		},

		listenForExternalExtensionStateRequest: function() {
			var base = this;

			$(window.document).on('adapt::query/request-state', function() {
				$(window.document).trigger(
					'adapt::query/response',
					[base.getResponseObject('ExtensionState')]
				);
			});
		},

		listenForExternalCollectionRequest: function() {
			var base = this;

			$(window.document).on('adapt::query/request-collections', function() {
					$(window.document).trigger(
						'adapt::query/response',
						[base.getResponseObject('Collection')]
					);
			});
		},

		listenForExternalRegistrationRequest: function() {
			var base = this;

			$(window.document).on('adapt::query/request-registration', function() {
					base.registerToExternalListeners();
			});
		},

		listenForExternalDeletionRequest: function() {
			var base = this;

			$(window.document).on('adapt::query/request-deletion', function(e, data) {
				if (base._validateEventData(data)) {
					base.deleteComment(data.payload);
				}
			});
		},

		setupExternalEvents: function() {
			this.listenForExternalExtensionStateRequest();
			this.listenForExternalCollectionRequest();
			this.listenForExternalRegistrationRequest();
			this.listenForExternalDeletionRequest();
		},

		requestCommentDeletion: function(_comment) {
			$(window.document).trigger('adapt::query/deletion-approval-request', [{
				target: 'commentsList-' + this.block_id,
				payload: _comment
			}]);
		},

		_reportExtensionStateChanged: function() {
			this.initialStateChanged = true;
			$(window.document).trigger('adapt::query/state-changed');
		},

		_eventNotRegistered: function(eventName) {
			return !$._data(window.document,'events')
					.hasOwnProperty('adapt::comments-list/'+eventName);
		},

		_validateEventData: function(data) {
			return	data &&
							typeof data === "object" &&
							data.hasOwnProperty('target') &&
							data.hasOwnProperty('payload') &&
							data.target === "commentsList-" + this.block_id &&
							typeof data.payload === 'object' &&
							data.payload.hasOwnProperty('id')  &&
							data.payload.hasOwnProperty('author_id')  &&
							data.payload.author_id === this.params.current_user_id;
		},

		setLayout: function() {
			if (this.params.togglable) {
				this.commentsListContainer.hide();
			} else {
				this.commentsListContainer.show();
			}
		},

		onSpecButtonClicked: function(event, preventEvent) {
			preventEvent = preventEvent || true;
			if (event && preventEvent) event.preventDefault();

			if (!this.commentsListContainer.hasClass('widget-open')) {
				this.displayCommentsList();
			} else {
				this.hideCommentsList();
			}
		},

		displayCommentsList: function() {
			this.commentsListContainer.show().addClass('widget-open');
			this.toggleButton.hide();
			this.commentsListContainer.a11y_focus();
			Adapt.trigger('comments-list-extension-widget:open', this.model.get('_id'));
		},

		hideCommentsList: function() {
			if (this.commentsListContainer.is(':visible')) {
				this.commentsListContainer.hide().removeClass('widget-open');
				this.toggleButton.show();
			}
		},

		checkIfShouldClose: function(id) {
			if (this.model.get('_id') !== id) {
				this.hideCommentsList();
			}
		},

		_GUID: function() {
			//http://stackoverflow.com/a/2117523
			return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(
				/[xy]/g,
				function(c) {
					var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
					return v.toString(16);
				}
			);
		},

		// http://stackoverflow.com/a/6234804
		_escapeHtml: function(unsafe) {
			return unsafe
					.replace(/&/g, "&amp;")
					.replace(/</g, "&lt;")
					.replace(/>/g, "&gt;")
					.replace(/"/g, "&quot;")
					.replace(/'/g, "&#039;");
		},

		_getCommentContent: function() {
			return this._escapeHtml(this.newComment.val()).replace(/\n/g, '<br/>');
		},

	});

	return CommentsListExtensionView;
});

