define('extensions/adapt-validatorComments/js/adapt-validatorComments',['require','coreJS/adapt','./adapt-validatorComments-Validation','./adapt-validatorCommentsView'],function(require) {

	var Adapt = require('coreJS/adapt');
	var Validation = require('./adapt-validatorComments-Validation');
	var ValidatorCommentsView = require('./adapt-validatorCommentsView');

		Adapt.on('blockView:postRender', function(view) {
			var globalParams = Adapt.course.get('_globals')._extensions._validatorComments;

			if (
				Validation.validateGlobalParams(globalParams) &&
				globalParams.is_enabled &&
				view.model.has('validatorComments') &&
				view.model.get('validatorComments')
			) {
				var validatorComments = view.model.get('validatorComments');
				if (Validation.validateExtensionParams(validatorComments)) {
					new ValidatorCommentsView({
						model: view.model
					});
				}
			}
	});
});

