define('extensions/adapt-validatorComments/js/adapt-validatorComments-Validation',[],function() {
	return {
		validateGlobalParams: function(globalParams) {
			return globalParams &&
				typeof globalParams === "object" &&
				globalParams.hasOwnProperty('is_enabled') &&
				typeof globalParams.is_enabled === "boolean" &&
				globalParams.hasOwnProperty('editable') &&
				typeof globalParams.editable === "boolean" &&
				globalParams.hasOwnProperty('draggable') &&
				typeof globalParams.draggable === "boolean" &&
				globalParams.hasOwnProperty('current_user_id') &&
				typeof globalParams.current_user_id === "string" &&
				globalParams.hasOwnProperty('current_user_name') &&
				typeof globalParams.current_user_name === "string" &&
				globalParams.hasOwnProperty('openButtonText') &&
				typeof globalParams.openButtonText === "string"
		},
		validateExtensionParams: function(params) {
			return params &&
				typeof params === "object" &&
				params.hasOwnProperty('stickyNotes') &&
				params.stickyNotes instanceof Array
		}
	};
});

