define('extensions/adapt-annotation/js/adapt-annotation',['require','jquery','underscore','coreJS/adapt','./utils/utils','./adapt-annotation/annotationsView','./adapt-annotationsControls/annotationsControlsView'],function(require) {

	var $ = require('jquery');
	var _ = require('underscore');
	var Adapt = require('coreJS/adapt');
	var Utils = require('./utils/utils');
	var AnnotationsView = require('./adapt-annotation/annotationsView');
	var AnnotationsControls = require('./adapt-annotationsControls/annotationsControlsView');
	var componentsToExtend = [];
	var attachControlsTo = {};
	var readOnly;

	var setupAnnotations = function(view) {
		componentsToExtend.push(view.model);
		var displayControlModel = view.model.clone();
		displayControlModel.set('toggleID', view.model.get('_parentId'));
		if (!attachControlsTo.hasOwnProperty(view.model.get('_parentId'))) {
			attachControlsTo[view.model.get('_parentId')] = displayControlModel;
		}
	}

	Adapt.on('componentView:postRender', function(view) {
		readOnly = Utils.getGlobalParams('_annotations') ? !Utils.getGlobalParams('_annotations').editable : true;
		if (Utils.isExtensionValidAndActive(view.model, '_annotations')) {
			switch(view.model.get('_component')) {
				default:
					if (readOnly) {
						if (view.model.get('_annotations').length > 0) {
							setupAnnotations(view);
						}
					} else {
						setupAnnotations(view);
					}
				break;
			}
		}
	});

	var DidYouKnow = function() {

		function _processBlockHeight(event) {
			var block = $(event.target).closest('.block');
			var newHeight = block.height();
			var fullComponents = block.find('.component-full');
			if (fullComponents.length > 0) {
				newHeight = $.makeArray(fullComponents)
					.reduce(function(computedHeight, component) {
						return computedHeight + $(component).outerHeight(true);
					}, block.find('.block-title').outerHeight(true) + 80);
			} else {
				newHeight = _.max($.makeArray(block.find('.component')), function(component) {
						return component.offsetHeight;
				}).offsetHeight;
				newHeight += block.find('.block-title').outerHeight(true) + 100;
			}
			block.height(newHeight);
		}

		return  {
			processBlockHeight: _processBlockHeight
		}
	}();

	var AccordionWithAudio = function() {

		function _processBlockHeight(event) {
			var block = $(event.target).closest('.block');
			var accordionTrigger = $(event.target).closest('.accordionWithAudio-item-title');

			if (accordionTrigger.hasClass('selected')) {
				accordionTrigger.siblings('.accordionWithAudio-item-body').css("height", "").height();
			} else {
				accordionTrigger.siblings('.accordionWithAudio-item-body').hide();
			}
			var newHeight = block.height();
			var fullComponents = block.find('.component-full');
			if (fullComponents.length > 0) {
				newHeight = $.makeArray(fullComponents)
					.reduce(function(computedHeight, component) {
						return computedHeight + $(component).outerHeight(true);
					}, block.find('.block-title').outerHeight(true) + 80);
			} else {
				newHeight = _.max($.makeArray(block.find('.component')), function(component) {
						return component.offsetHeight;
				}).offsetHeight;
				newHeight += block.find('.block-title').outerHeight(true) + 100;
			}
			block.height(newHeight);
		}

		return  {
			processBlockHeight: _processBlockHeight
		}
	}();

	Adapt.on('pageView:ready', function(){
		if (Utils.isGlobalParamsValidFor('_annotations')) {
			Object.keys(attachControlsTo).forEach(function(blockID){
				var model = attachControlsTo[blockID];
				model.attributes.toggleID = blockID;
				new AnnotationsControls({
					model: model
				});
			});
			componentsToExtend.forEach(function(model) {
				new AnnotationsView({
					model: model
				});
			});
			$(window.document)
			.on('click', 'button.didYouKnow-button', DidYouKnow.processBlockHeight)
			.on('click', '.accordionWithAudio-item-title', AccordionWithAudio.processBlockHeight);
		}
	});
});

