define('extensions/adapt-reviewText/js/adapt-reviewTextView',['require','jquery','backbone','coreJS/adapt','./utils/utils'],function(require) {

	var $ = require('jquery');
	var Backbone = require('backbone');
	var Adapt = require('coreJS/adapt');
	var Utils = require('./utils/utils');

    var ReviewTextView = Backbone.View.extend({

        className: "ck-editor-extension",
        initialize: function () {
            this.setupAttributes();
            this.removeEventILTBinded();
            this.initializeCkEditor();

            // On initialize start the render process
            this.preRender();
            this.render();
        },

        events: {},

        ckID: false,
        ckEditor: false,

        removeEventILTBinded: function() {
            $(this._view).off("dblclick");
        },

        setupAttributes: function() {
            this.componentId = this.model.get('_componentId');
            this.type = this.model.get('_type');
            this.component = this.model.get('_component');
            this._view = this.model.get('_view');
            this.ckID = Utils._GUID();
        },

        preRender: function() {},

        render: function () {
            return this;
        },

        getckEditorData: function() {
            return CKEDITOR.instances[this.ckID].getData();
        },

        hideToolbar: function() {
            $('#cke_'+this.ckID).addClass('hideToolbar');
        },

        enableReadOnlyMode: function() {
            if(Utils.getGlobalParams('_reviewText').editable === false) {
                return CKEDITOR.instances[this.ckID].setReadOnly(true);
            }
        },

        initializeCkEditor: function() {
            if(Utils.checkIsEditable('_reviewText')){
                $(this._view).attr('contenteditable', true);
            }
            $(this._view).attr('id', this.ckID);
            $(this._view).attr('data-id', this.ckID);
            this.ckEditor = CKEDITOR.inline(this.ckID);

            this.updateText(false);     //set false so initially we can get text at starting time before any editing.
            this.ckEditor.on('change', $.proxy(this.updateText, this, true));

            this.customConfigureOfCkEditor();
        },

        customConfigureOfCkEditor: function() {
            var base = this;
            this.ckEditor.on("instanceReady", function(e, data)
            {
                base.setCurrentUser();
                base.hideToolbar();
                base.enableReadOnlyMode();
            });
        },

        updateText: function(stateChange) {
            var newText = this.getckEditorData();
            if(stateChange) {
                if(newText != this.model.get('oldText')){
                    this.model.set('text', newText);
                    this.model.set('oldText', newText);
                    Adapt.trigger('reviewText::textChanged-' + this.componentId);
                }
            } else {
                this.model.set('oldText', newText);
                this.model.set('text', newText);
            }
        },

        setCurrentUser: function() {
            var globalParams = Adapt.course.get('_globals')._extensions._reviewText;
            var user = {
                name: globalParams.current_user_name,
                id : globalParams.current_user_id
            };

            var editor = this.ckEditor;
            if (user && editor) {
                var lite = editor.plugins.lite;
                lite && lite.findPlugin(editor).setUserInfo(user);
            }
        },
    });
    
    // Return ReviewTextView so it can be required
    return ReviewTextView;

});

