define('components/adapt-Tables/js/generic',['require','coreJS/adapt','coreViews/componentView'],function(require) {
    var Adapt = require('coreJS/adapt');
    var ComponentView = require('coreViews/componentView');
    var Generic = ComponentView.extend({

        preRender: function() {
            var isLayoutFull = true;
            var layout = this.model.get('_layout');
            if(layout == "right" || layout == "left"){
               isLayoutFull = false;
            }
            this.model.set('_isRendered',isLayoutFull);
            this.model.set('_isLayoutFull',isLayoutFull);
            this.setCell();
        },

        setCell: function() {
            var rows = this.model.get('table').rows;//console.log("setCell ",rows);
            //var rowInfo = [];
            if(rows[0].rowData[0].data == undefined){
                _.each(rows,function(row) {
                    var rowData = [];//console.log("rowData ",row.rowData);
                    //if(row.rowData.data == undefined){console.log("rowData ",rowData);
                        _.each(row.rowData,function(newData,index) {
                            var data = {
                                    data:newData,
                                    rowspan : 1,
                                    colspan : 1,
                                    _isInvisible : false
                                };
                            rowData.push(data);
                        });
                    /*} else {
                        rowData = row.rowData;
                    }*/
                    _.each(row.cellInfo,function(cell,index) {
                        if(cell.mergeType == "row"){
                            //if(rowData.length <= cell.cellNumber-1)
                                rowData[cell.cellNumber-1].colspan = cell.merge;
                        }
                        if(cell.mergeType == "column") {
                            rowData[cell.cellNumber-1].rowspan = cell.merge;
                        }
                    });
                    //console.log("RowData ",rowData);
                    //rowInfo.push(rowData);
                    row.rowData = rowData;
                    //this.model.set("rowData",rowData);
                });
            }
            //console.log("rows ",rows);
            _.each(rows,function(row,ind) {
                _.each(row.rowData,function(info,index) {
                    if(info.colspan != 1) {
                        var i = 0;
                        for(i = 1; ((i < info.colspan) && (index+i < row.rowData.length)); i++) {
                            row.rowData[index+i]._isInvisible = true;
                        }
                        if(index+i >= row.rowData.length){//console.log("in colspan");
                            rows[ind].rowData[index].colspan = i--;
                        }
                    }
                    if(info.rowspan != 1) {
                        var i = 0;
                        for(i=1; ((i < info.rowspan) && (ind+i < rows.length)); i++) {
                            rows[ind+i].rowData[index]._isInvisible = true;
                        }
                        if(ind+i >= rows.length){//console.log("in rowspan");
                            rows[ind].rowData[index].rowspan = i--;
                        }
                    }
                });
            });
            //console.log("rows ",rows);
        },

        postRender: function() {
            //console.log("postRender called");
            if(!this.model.get('_isRendered')) {
                this.model.set('_isRendered',true);
                this.setTable();
            }
            this.setReadyStatus();
        },

        setTable: function() {
            var $currentComponent = this.$('.component-inner').closest('.component');
            //console.log("currentComponent ",$currentComponent);
            //console.log("current ",this.$el);
            var currentComponentWidth = $currentComponent.width();
            //console.log("currentComponentWidth ",currentComponentWidth);
            $currentComponent.addClass('component-temp');
            //console.log("currentComponentWidth ",$currentComponent.width());
            var columns = this.model.get('table').columns;
            var rows = this.model.get('table').rows;//console.log("setTable ",rows);
            var columnsWidth = [];
            var totalColumnsWidth = 0;
            _.each(columns,function(column,index) {
                //console.log(index);
                var isFixed,width;
                //console.log(this.$('#'+index));
                width = this.$('#'+index).outerWidth();//this.$('th').eq(index).outerWidth();
                //console.log("index "+index+ " "+width);
                if(column.width == undefined || column.width == "")
                    isFixed = false;
                else
                    isFixed = true;
                columnsWidth.push({
                    width:width,
                    isFixed:isFixed
                });
                totalColumnsWidth +=width;
                /*if(!isFixed) {
                    fixedColumnsWidth += width;
                }*/
                //console.log(this.$('th').eq(index));
            });
            //console.log(columnsWidth);
            //console.log(totalColumnsWidth);
            //console.log(columns.length);
            while(totalColumnsWidth > currentComponentWidth) {
                var poppedColumn = columnsWidth.pop();
                totalColumnsWidth -= poppedColumn.width;
            }
            //console.log(columnsWidth.length);
            columns.length = columnsWidth.length;
            //console.log(columns.length);
            _.each(rows,function(row,index) {
                row.rowData.length = columnsWidth.length;
            });
            //$currentComponent.removeClass('component-temp');
            this.setCell();
            //console.log("after setTable ",columns);
            this.render();
            $currentComponent.width(currentComponentWidth);
            //console.log(columns.length);
            //console.log(rows);
        }

    },{
        template:'generic'
    });

    Adapt.register("generic", Generic);
    return Generic;
});

