define('extensions/adapt-annotation/js/utils/utils',['require','coreJS/adapt','../../assets/moment-timezone'],function(require) {
	var Adapt = require('coreJS/adapt');
	var moment = require('../../assets/moment-timezone');

	var _getGlobalParams =  function(extensionName) {
		return Adapt.course.get('_globals')._extensions[extensionName];
	};

	var	_isGlobalParamsValid = function(extensionName) {
		var globalParams = _getGlobalParams(extensionName);

		return globalParams &&
			typeof globalParams === "object" &&
			globalParams.hasOwnProperty('is_enabled') &&
			typeof globalParams.is_enabled === "boolean" &&
			globalParams.is_enabled &&
			globalParams.hasOwnProperty('editable') &&
			typeof globalParams.editable === "boolean" &&
			globalParams.hasOwnProperty('current_user_id') &&
			typeof globalParams.current_user_id === "string" &&
			globalParams.hasOwnProperty('current_user_name') &&
			typeof globalParams.current_user_name === "string";
	};

	var _isExtensionParamsValid = function(model, extensionName) {
		if (model.has(extensionName)) {
			var params = model.get(extensionName);

			return params &&
				typeof params === "object";
		}

		return false;
	};

	var _guid = function() {
		//http://stackoverflow.com/a/2117523
		return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(
			/[xy]/g,
			function(c) {
				var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
				return v.toString(16);
			}
		);
	};

	var _escapeHtml = function(unsafe) {
		// http://stackoverflow.com/a/6234804
		return unsafe
				.replace(/&/g, "&amp;")
				.replace(/</g, "&lt;")
				.replace(/>/g, "&gt;")
				.replace(/"/g, "&quot;")
				.replace(/'/g, "&#039;");
	};

	var _replaceHtmlChar = function(unsafe) {
		// http://stackoverflow.com/a/6234804
		return unsafe
				.replace(/&amp;/g, "&")
				.replace(/&lt;/g, "<")
				.replace(/&gt;/g, ">")
				.replace(/&quot;/g, "\"")
				.replace(/&#039;/g, "\'");
	};

	var _formatDate = function(date) {
		return moment(date, 'America/Toronto').format('MM/DD/YYYY  HH:mm');
	};

	return {
		isGlobalParamsValidFor: _isGlobalParamsValid,
		getGlobalParams: _getGlobalParams,
		guid: _guid,
		isExtensionParamsValid: _isExtensionParamsValid,
		escapeHtml: _escapeHtml,
		revertHtmlChars: _replaceHtmlChar,
		formatDate: _formatDate,
		isExtensionValidAndActive: function(model, extensionName) {
			return _isGlobalParamsValid(extensionName) &&
				_isExtensionParamsValid(model, extensionName);
		},
	};
});

