define('extensions/adapt-cleoBlockBullets/js/utils/utils',['require','coreJS/adapt'],function(require){

    var Adapt = require('coreJS/adapt');

	return {
		_GUID: function() {
            //http://stackoverflow.com/a/2117523
            return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(
                /[xy]/g,
                function(c) {
                    var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
                    return v.toString(16);
                }
            );
        },

        getGlobalParams: function(extensionName) {
            return Adapt.course.get('_globals')._extensions[extensionName];
        },

        checkIsEnable: function(extensionName) {
            var extensionData = this.getGlobalParams(extensionName);
            return  extensionData &&
                    extensionData.hasOwnProperty('_isEnabled') &&
                    extensionData._isEnabled;
        }
	};
});

