define('extensions/adapt-EOM-pageLevelProgress/js/blockNumericProgressView',['require','coreJS/adapt','backbone'],function(require) {

    var Adapt = require('coreJS/adapt');
    var Backbone = require('backbone');

    var BlockNumericProgressView = Backbone.View.extend({

        className: 'page-level-progress-navigation-completion',

        initialize: function() {
            this.listenTo(Adapt, 'remove', this.remove);
            this.listenTo(Adapt.blocks, 'change:_isComplete', this.componentChangedAttribute);
            this.render();
            this.updateBlocksCompletedOutOF();
        },
        events: {
            //'click .top-navigator-arrow': 'scrollToPageToTop'
        },
        onPageViewReady: function() {
            this.render();
        },
        componentChangedAttribute: function(model) {
            if (_.contains(this.getBlockNames(), model.get('_id'))) {
                this.updateBlocksCompletedOutOF();
            }
        },
        updateBlocksCompletedOutOF: function() {
            var noOfBlocks = this.getTotalBlocks().length;
            var completedBlocks = this.getCompletionBlocks(); //this.model.get("blockComplate");
            var complateOutOfTotal = this.getPadded(completedBlocks) + '/' + this.getPadded(noOfBlocks);
            this.$('.page-level-progress-numericCount-bar').html(complateOutOfTotal);
            this.$('.page-level-progress-numericCount-bar-mask').html(complateOutOfTotal);
        },
        getPadded: function(num) {
            return num > 9 ? num : '0' + num;
        },
        getBlockNames: function() {
            var blocksName = [];
            _.each(this.getTotalBlocks(), function(obj) {
                blocksName.push(obj.get('_id'));
            });
            return blocksName;
        },
        getTotalBlocks: function() {
            return this.model.findDescendants('blocks').where({
                '_isAvailable': true
            });
        },
        /*  resetCompletionStatusOfBlocks:function(){
             _.each(this.getTotalBlocks(), function(obj) {
                    obj.set('_isInteractionComplete',false);
                    obj.set('_isComplete',false);
                });
            },*/
        getCompletionBlocks: function() {
            var count = 0;
            _.each(this.getTotalBlocks(), function(obj) {
                if (obj.get('_isComplete') /*&&obj.get('_isIntractionComplete')*/ )
                    count++
            });
            return count;
        },
        render: function() {
            var data = this.model.toJSON();
            var template = Handlebars.templates["blockNumericProgress"];
            var isTopNavigator = Adapt.course.get('_eomPageLevelProgress');
            if (isTopNavigator && isTopNavigator._isEnabled) {
                this.$el.html(template(data));
            }
            return this;
        }
    });

    return BlockNumericProgressView;

});
