define('extensions/adapt-commentsList/js/adapt-commentsCollection',['require','backbone','./adapt-commentModel'],function(require) {
	var Backbone = require('backbone');
	var CommentModel = require('./adapt-commentModel');

	var CommentsCollection = Backbone.Collection.extend({
		model: CommentModel,
		comparator: function(model) {
			return +model.get('timestamp');
	   	}
	});

	return CommentsCollection;
});

