define('extensions/adapt-windowClick/js/adapt-windowClick',['require','jquery','coreJS/adapt'],function(require) {

	var $ = require('jquery');
	var Adapt = require('coreJS/adapt');

	function isListeningForWindowClickEvent(extension) {
		return extension &&
			extension.hasOwnProperty('windowClick') &&
			extension.windowClick.hasOwnProperty('isEnabled') &&
			extension.windowClick.isEnabled &&
			extension.windowClick.hasOwnProperty('listeningOn');
	}

	Adapt.on('articleView:postRender', function() {
		$(window).on('click', function(event) {
			if ($(event.target).closest(".extension-inner").length === 0) {
				if (!$(event.target).hasClass('extension-action')) {
					var extensions = Adapt.course.get('_globals')._extensions;

					Object.keys(extensions).forEach(function(extension){
						if (isListeningForWindowClickEvent(extensions[extension])) {
							Adapt.trigger(extensions[extension].windowClick.listeningOn);
						}
					});
				}
			}
		});
	});
});

