define('extensions/adapt-validatorComments/js/adapt-validatorCommentsList/js/adapt-validatorCommentsCollection',['require','backbone','./adapt-validatorCommentModel'],function(require) {
	var Backbone = require('backbone');
	var CommentModel = require('./adapt-validatorCommentModel');

	var CommentsCollection = Backbone.Collection.extend({
		model: CommentModel,
		comparator: function(model) {
			return +model.get('timestamp');
	   	}
	});

	return CommentsCollection;
});

