Modernizr.load ([{
    test: true,
    yep: 'assets/ckeditor.js'
}]);

define('extensions/adapt-reviewText/js/adapt-reviewText',['require','coreJS/adapt','./adapt-reviewTextsView','./utils/utils','./utils/ckEditor-config'],function(require) {

    var Adapt = require('coreJS/adapt');
    var ReviewTextsView = require('./adapt-reviewTextsView');
    var Utils = require('./utils/utils');
    var ckEditorConfig  = require('./utils/ckEditor-config');
    var authors = [];
    var ckEditors = [];
    
    // view argument represent the component view
	Adapt.on('componentView:postRender', function(view) {
		var extensionData = Utils.getGlobalParams('_reviewText');
		if ( extensionData &&
			 extensionData.hasOwnProperty('_isEnabled') &&
			 extensionData._isEnabled
		) {
			var reviewText = view.model.get('_reviewText');
			if ( reviewText && reviewText.hasOwnProperty('_isEnabled') && reviewText._isEnabled === true) {
				view.model._componentView = view.el;
				ckEditors.push(view.model);
			}
		}
	});

	Adapt.on('blockView:postRender', function(view) {
		var extensionData = Utils.getGlobalParams('_reviewText');
		if ( extensionData &&
			 extensionData.hasOwnProperty('_isEnabled') &&
			 extensionData._isEnabled
		) {
			var reviewText = view.model.get('_reviewText');
			if ( reviewText && reviewText.hasOwnProperty('_isEnabled') && reviewText._isEnabled === true) {
				view.model._componentView = view.el;
				ckEditors.push(view.model);
			}
		}
	});

	Adapt.on('pageView:ready', function(view) {
		var extensionData = Utils.getGlobalParams('_reviewText');
		if ( extensionData &&
			 extensionData.hasOwnProperty('_isEnabled') &&
			 extensionData._isEnabled
		) {
			var styleCount = 1;
			if(extensionData.hasOwnProperty('authors') && extensionData.authors.length > 0) {
				_.each(extensionData.authors, function(item, index) {
					authors[item.id] = styleCount++;
				});
			} else {
				authors[extensionData.current_user_id] = 1;
			}
			ckEditorConfig.init(authors);
        	initializeCkEditors();
		}
	});

	function initializeCkEditors() {
		_.each(ckEditors, function(element, index) {
			new ReviewTextsView({
				model: element
			});
		});
	}

});

