define('extensions/adapt-validatorComments/js/adapt-validatorCommentsList/js/adapt-validatorCommentModel',['require','backbone','../assets/moment-timezone'],function(require) {

	var Backbone = require('backbone');
	var moment = require('../assets/moment-timezone');

	var Comment = Backbone.Model.extend({
		defaults: {
			"id": "",
			"block_id": "",
			"current_user_id": "",
			"owner": true,
			"author_id": "",
			"author_name": "",
			"comment": "",
			"date": "",
			"timestamp": new Date().getTime(),
			"editable": false,
			"deletion_status": false,
			"edition_mode": false,
			"changed": false
		},

		initialize: function() {
			this._parseTimestamp();

			this.on('change:timestamp', this._parseTimestamp, this);
		},

		_parseTimestamp: function() {
			var parsedDate = new Date(parseInt(this.get('timestamp'), 10));
			var formatedDate = this._formatDate(parsedDate);

			this.set('date', formatedDate);
		},

		_formatDate: function(date) {
			return moment(date, 'America/Toronto').format('MM/DD/YYYY  HH:mm');
		},

	});

	return Comment;
});

