define('extensions/adapt-annotation/js/adapt-annotation/annotationsView',['require','coreJS/adapt','jquery','underscore','jqueryUI','backbone','../utils/utils','./annotationView','./annotation','./annotations','../../assets/ResizeObserver'],function(require) {

	var Adapt = require('coreJS/adapt');
	var $ = require('jquery');
	var _ = require('underscore');
	var jqueryUI = require('jqueryUI'); //eslint-disable-line
	var Backbone = require('backbone');
	var Utils = require('../utils/utils');
	var AnnotationView = require('./annotationView');
	var Annotation = require('./annotation');
	var Annotations = require('./annotations');
	var ResizeObserver = require('../../assets/ResizeObserver');


	var AnnotationsView = Backbone.View.extend({
		events: {
			'mousedown .annotations-extension': 'handleDrawingNewAnnotationShape'
		},
		templateParams: {
			displayAnnotations: true
		},
		params: {},
		component_id: undefined,
		collection: [],
		initialStateChanged: false,
		dom: false,
		annotationsShown: true,
		initialBlockHeight: 0,

		initialize: function() {

			this.setupAttributes();

			if (this.params.is_enabled) {

				this.setupCollection();

				if (this.collection.length <= 0 && !this.params.editable) {
					this.hideAnnotationsDisplayControls();
				} else {
					this.setupTemplateParams();
					this.setupEvents();
					this.render();
					this.moveAnnotationsDisplayControls(true);
				}
			}
		},

		render: function() {
			this.renderTemplate();
			this.saveDOMRefs();
			this.addAllAnnotationsToView();
			_.defer(_.bind(this.postRender, this));

			return this;
		},

		postRender: function() {
			this.watchResizeEvents();
			this.setDefaultRenderState();
			this.processRightComponents();
			this.fixDidYouKnowRelatedDisplay();
			this.listenForAccordion();
		},

		listenForAccordion: function() {
			var base = this;

			this.$el.closest('.block')
			.on('click', '.accordion-item-title', function() {
				base.fixDidYouKnowRelatedDisplay($(this));
			});
		},

		fixDidYouKnowRelatedDisplay: function(accordionTrigger) {
			if (_.contains(['accordionWithAudio', 'accordion'], this.model.get('_component'))) {
				this.fixAccordionLayout(accordionTrigger);
			}
			if (this.model.get('_component') === "narrative") {
					this.fixNarrativeLayout();
			}
		},

		fixAccordionLayout: function(accordionTrigger) {
			var block = this.$el.closest('.block');
			if (block && block.length > 0) {
				var currentHeight = block.height();
				this.initialBlockHeight = this.initialBlockHeight || currentHeight;

				if (accordionTrigger && accordionTrigger.length > 0) {
					var triggerBody = accordionTrigger.siblings('.accordion-item-body');
					var triggerBodyHeight = triggerBody.css("height", "").height();
					var selectedAccordions = block.find('.accordion-item-body:visible');
					var currentComponent = accordionTrigger.closest('.component');
					var nextComponentHeight = currentComponent.siblings().height();

					if (accordionTrigger.hasClass("selected")) {
						var otherVisibleParts = currentComponent.find('.accordion-item-body:visible');
						var sizeToRemove = 0;

						otherVisibleParts.each(function(index, element) {
							sizeToRemove += $(element).height();
						});

						var thisComponentHeight = currentComponent.height() - sizeToRemove + triggerBodyHeight;

						currentHeight = (thisComponentHeight >= nextComponentHeight) ? thisComponentHeight : nextComponentHeight;

						currentHeight += 100;

					} else {
						var reducedHeight = currentHeight - triggerBodyHeight - 100;
						if (selectedAccordions.length > 1) {
							selectedAccordions.each(function(index, element) {
								if (!$(element).is(triggerBody)) {
									currentHeight = $(element).closest('.component').height() + 100;
								}
							});
						} else {
							if (reducedHeight < this.initialBlockHeight) {
								currentHeight = this.initialBlockHeight;
							} else {
								if (reducedHeight < currentHeight);
								currentHeight = reducedHeight;
							}
						}
						nextComponentHeight += 100;
						currentHeight = (currentHeight >= nextComponentHeight) ? currentHeight : nextComponentHeight;
					}
				}

				block.height(currentHeight);
				block.find('.accordion-component').css({
					"margin-left": "2px",
					"margin-bottom": "50px"
				});

				var components = block.find('.component');
				if (components && components.length > 1) {
					$(components[1]).css({
						"width": "49%"
					});
				}
			}
		},

		fixNarrativeLayout: function() {
			var block = this.$el.closest('.block');
			var computedHeight = block.height();
			if (this.model.get('_layout') !== 'full') {
				computedHeight += 15;
			} else {
				computedHeight += 100;
			}
			block.height(computedHeight);
			block.find('.narrative-component').css({
				"margin-left": "2px",
				"margin-right": "2px"
			});
			block.find('.narrative-slider-graphic ').each(function(index, element) {
				$(element).width($(element).width() - 2);
			});
		},

		hideAnnotationsDisplayControls: function() {
			Adapt.trigger('annotationsDisplayControls::hide-' + this.model.get('_id'));
		},

		processRightComponents: function() {
			if (this.model.get('_layout') === 'right') {
				this.$('.annotations-extension').css({
					"z-index": 9999
				});
			}
		},

		setDefaultRenderState: function() {
			var base = this;

			if (this.model.get('_layout') === 'right') {
				if (this.leftComponentHasAnnotationExtension()) {
					Adapt.on('annotations::collectionSize-' + this.model.get('_parentId'), function(neighborCollectionSize) {
						if (neighborCollectionSize === 0) {
							base.processDrawingZoneState();
						}
					});
					Adapt.trigger('annotations::requestCollectionSize-' + this.model.get('_parentId'));
				} else {
					base.processDrawingZoneState();
				}
			} else {
				base.processDrawingZoneState();
			}
		},

		leftComponentHasAnnotationExtension: function() {
			return !$('.'+ this.model.get('_parentId'))
				.find('.annotations-extension:first')
				.closest('.component')
				.hasClass(this.component_id);
		},

		processDrawingZoneState: function() {
			var base = this;

			Adapt.on('annotationsDisplayControls::status-' + this.model.get('_parentId'), function(shown) {
				if ((base.collection.length <= 0 && shown) || (base.collection.length > 0 && !shown)) {
					Adapt.trigger('annotationsDisplayControls::toggle-' + base.model.get('_parentId'));
				}
			});
			Adapt.trigger('annotationsDisplayControls::getStatus-' + this.model.get('_parentId'));
		},

		watchResizeEvents: function() {
			var base = this;
			var repositionControls = _.debounce(function() {
				base.moveAnnotationsDisplayControls();
			}, 5);

			var observer = new ResizeObserver(function(entries) {
				for (var i = entries.length; i--;) {
						repositionControls();
				}
			});

			var element = $('.' + this.model.get('_id'));

			if (element.length > 0) {
				observer.observe(element[0]);
			}
		},

		moveAnnotationsDisplayControls: function() {

			var block = this.$el.closest('.block');
			var annotationsComponents = block.find('.annotations-extension');
			var button = $('#annotationsDisplayControlsForBlock-' + this.model.get('_id'));

			if (annotationsComponents.length > 0) {
				var firstComponent = $(annotationsComponents[0].closest('.component'));

				if (firstComponent.length > 0) {
					var firstDrawingContainer = $(firstComponent.find('.annotations-extension'));

					if (firstDrawingContainer.length > 0) {
						button.offset({
							top: $('.'+this.model.get('_id')).offset().top + $('.'+this.model.get('_id')).height()
						});
					}
				}
			}

			if (this.model.get('_layout') === 'full') {
				button.css({
					margin: 0
				});
			}
		},

		setupTemplateParams: function() {
			this.templateParams = {
				_id: this.component_id
			};
		},

		renderTemplate: function() {
			var template = Handlebars.templates['annotations'];
			this.$el.html(template(this.templateParams))
				.appendTo($('.' + this.model.get('_id')));
		},

		setupAttributes: function() {
			this.component_id = this.model.get('_id');
			this.params = Utils.getGlobalParams('_annotations');
			this.data = this.model.toJSON();
			this.dom = this.getDomRefsObject();
			this.annotationsShown = true;
		},

		getDomRefsObject: function() {
			return {
				drawingContainer: false,
			};
		},

		saveDOMRefs: function() {
			this.dom.drawingContainer = this.$('#annotation-container-' + this.component_id);
		},

		setupCollection: function() {
			if (this.data._annotations.length > 0) {
				this.collection = new Annotations(this.data._annotations);
			} else {
				this.collection = new Annotations();
			}
		},

		setupEvents: function() {
			this.setupLocalEvents();
			this.setupExternalEvents();
			this.registerToExternalListeners();
		},

		registerToExternalListeners: function() {
			var base = this;

			$(window.document).trigger('adapt::register', [{
				"extension-name": 'annotations',
				"extension-id": base.component_id,
			}]);

			return this;
		},

		listenForExternalExtensionStateRequest: function() {
			var base = this;

			$(window.document).on('adapt::query/request-state', function() {
				$(window.document).trigger(
					'adapt::query/response',
					[base.getResponseObject('ExtensionState')]
				);
			});
		},

		listenForExternalCollectionRequest: function() {
			var base = this;

			$(window.document).on('adapt::query/request-collections', function() {
					$(window.document).trigger(
						'adapt::query/response',
						[base.getResponseObject('Collection')]
					);
			});
		},

		listenForExternalRegistrationRequest: function() {
			var base = this;

			$(window.document).on('adapt::query/request-registration', function() {
					base.registerToExternalListeners();
			});
		},

		listenForExternalDeletionRequest: function() {
			var base = this;

			$(window.document).on('adapt::query/request-deletion', function(e, data) {
				if (base.validateEventData(data)) {
					base.deleteComment(data.payload);
				}
			});
		},

		deleteComment: function(_element) {
			if (this.elementBelongsToUser(_element)) {
				var element = this.collection.get(_element.id);
				if (element) {
					this.collection.remove(element);
					Adapt.trigger('annotations::requestDestruction-' + _element.id);
					this.reportExtensionStateChanged();
				}
			}
		},

		reportExtensionStateChanged: function() {
			this.initialStateChanged = true;
			$(window.document).trigger('adapt::query/state-changed');
		},

		elementBelongsToUser: function(comment) {
			return comment.author_id === this.params.current_user_id;
		},

		validateEventData: function(data) {
			return	data &&
							typeof data === "object" &&
							data.hasOwnProperty('target') &&
							data.hasOwnProperty('payload') &&
							data.target === "annotation-" + this.component_id &&
							typeof data.payload === 'object' &&
							data.payload.hasOwnProperty('id')  &&
							data.payload.hasOwnProperty('author_id')  &&
							data.payload.author_id === this.params.current_user_id;
		},

		getResponseObject: function(type) {
			var responseObject = this.generateResponseObject();
			switch(type) {
				case 'ExtensionState':
					responseObject.response['state'] = this.initialStateChanged;
				break;
				case 'Collection':
					var filteredCollection = this.collection
						.chain()
						.filter(function(annotation) {
							return annotation.get('width') >= 10 && annotation.get('height') >= 10;
						})
						.invoke('toJSON')
						.value();

					responseObject.response['collection'] = filteredCollection;
				break;
			}
			return responseObject;
		},

		generateResponseObject: function() {
			return {
				"extension-name": 'annotations',
				"extension-id": this.component_id,
				response: {
					component_id: this.component_id
				}
			};
		},

		setupLocalEvents: function() {
			this.setupAdaptEvents()
			this.setupCollectionEvents();
		},

		setupAdaptEvents: function() {
			var base = this;

			this.listenTo(Adapt, 'remove', this.remove);

			Adapt.on('annotationsDisplayControls::rendered-' + this.model.get('_id'), $.proxy(base.moveAnnotationsDisplayControls, base));

			Adapt.on('annotations::request-deletion-' + this.component_id, $.proxy(base.requestDeletionApprovalFromExternalListeners, base));

			Adapt.on('annotations::state-changed-' + this.component_id, $.proxy(base.reportExtensionStateChanged, base));

			Adapt.on('annotations::requestCollectionSize-' + this.model.get('_parentId'),
				$.proxy(base.reportCollectionSize, base));

			this.setupDisplayToggleListeners();
		},

		reportCollectionSize: function() {

			if (this.model.get('_layout') === 'left') {
				Adapt.trigger('annotations::collectionSize-' + this.model.get('_parentId'),
					this.collection.length);
			}
		},

		setupDisplayToggleListeners: function() {
			var base = this;

			// Listen on block level
			Adapt.on('annotations::toggle-' + base.model.get('_parentId'), $.proxy(base.toggleDrawingContainer, base));
			// Listen on component level
			Adapt.on('annotations::toggle-' + base.model.get('_id'), $.proxy(base.toggleDrawingContainer, base));
			//Listen on layer level
			if (base.model.get('trigger')) {
				Adapt.on('annotations::toggle-' + base.model.get('_id') + '/' + base.model.get('trigger'), $.proxy(base.toggleDrawingContainer, base));
			}
		},

		toggleDrawingContainer: function() {
			this.annotationsShown = !this.annotationsShown;
			this.dom.drawingContainer.toggle();
			var component = this.$el.closest('.component')
			if (this.annotationsShown) {
				component.css("z-index", 'initial');
			} else {
				component.css("z-index", 2);
			}
			if (this.model.get('_layout') === 'full') {
				this.$('.annotations-extension-countour-full').toggle();
			} else {
				this.$('.annotations-extension-countour').toggle();
			}

		},

		requestDeletionApprovalFromExternalListeners: function(element) {
			$(window.document).trigger('adapt::query/deletion-approval-request', [{
				target: 'annotation-' + this.component_id,
				payload: element
			}]);

			return this;
		},

		setupCollectionEvents: function() {
			this.listenTo(this.collection, 'reset', this.addAllAnnotationsToView);
			this.listenTo(this.collection, 'add', this.addAnnotationToView);

			return this;
		},

		requestDeletion: function(_element) {
			$(window.document).trigger('adapt::query/deletion-approval-request', [{
				target: 'annotation-' + this.component_id,
				payload: _element
			}]);
		},

		setupExternalEvents: function() {
			this.listenForExternalExtensionStateRequest();
			this.listenForExternalCollectionRequest();
			this.listenForExternalRegistrationRequest();
			this.listenForExternalDeletionRequest();
		},

		handleDrawingNewAnnotationShape: function(event) {
			$(window).trigger('click');

			if (this.canStartDrawing(event)) {
				this.collection.add(
					this.generateNewAnnotation(
						this.getShapeCoordinates(event)
					)
				);

				this.reportExtensionStateChanged();
			}
		},

		canStartDrawing: function(event) {
			return this.annotationsShown &&
				!$(event.target).hasClass('annotation-ignore-click') &&
				this.params.editable &&
				event.which === 1;
		},

		addAnnotationToView: function(annotation) {
			annotation = this.processMissingAnnotationAttributes(annotation);
			var newAnnotationView = new AnnotationView({
				model: annotation
			});

			this.dom.drawingContainer.append(newAnnotationView.$el);
			newAnnotationView.$('.highlight').css(annotation.get('coordinates'));
		},

		processMissingAnnotationAttributes: function(annotation) {
			if (this.isMissingAttributes(annotation)) {
				annotation.attributes.component_id = this.component_id;
				annotation.attributes.drawingContainerCoordinates = this.dom.drawingContainer.offset();

				return annotation;
			} else {
				return annotation;
			}
		},

		isMissingAttributes: function(annotation) {
			return !(
				annotation.attributes.hasOwnProperty('component_id') &&
				annotation.attributes.hasOwnProperty('drawingContainerCoordinates') &&
				typeof annotation.attributes.drawingContainerCoordinates === 'object' &&
				annotation.attributes.drawingContainerCoordinates.hasOwnProperty('top') &&
				annotation.attributes.drawingContainerCoordinates.hasOwnProperty('left')
			);
		},

		addAllAnnotationsToView: function() {
			Adapt.trigger('annotations::requestDestructionOfAllViews-' + this.component_id);
			this.collection.each(this.addAnnotationToView, this);
		},

		generateNewAnnotation: function(coordinates) {
			return new Annotation({
				id: Utils.guid(),
				component_id: this.component_id,
				author_id: this.params.current_user_id,
				author_name: this.params.current_user_name,
				editable: this.params.editable,
				coordinates: coordinates,
				drawingContainerCoordinates: this.dom.drawingContainer.offset(),
				drawingContainer: this.dom.drawingContainer,
				ownAnnotation: true,
				timestamp: new Date().getTime()
			});
		},

		getShapeCoordinates: function(event) {
			var drawingContainerCoordinates = this.dom.drawingContainer.offset();

			return {
				top: event.pageY - drawingContainerCoordinates.top,
				left: event.pageX - drawingContainerCoordinates.left
			};
		},

	});

	return AnnotationsView;

});

