define('extensions/adapt-annotation/js/adapt-annotationsControls/annotationsControlsView',['require','jquery','coreJS/adapt','backbone'],function(require) {

	var $ = require('jquery');
	var Adapt = require('coreJS/adapt');
	var Backbone = require('backbone');

	var AnnotationsControls = Backbone.View.extend({

		events: {
			'click .annotations-toggle-display': 'toggleAnnotationDisplayButton'
		},
		block_id: false,
		displayed: true,

		initialize: function() {
			this.setupAttributes();
			this.render();
		},

		render: function() {
			this.renderTemplate();
			this.saveDOMRefs();
			this.setupEvents();
			this.reportDisplayControlRendered();

			return this;
		},

		setupEvents: function() {
			var base = this;

			Adapt.on('annotationsDisplayControls::toggle-' + base.model.get('toggleID'), $.proxy(base.toggleAnnotationDisplayButton, base));
			Adapt.on('annotationsDisplayControls::getStatus-' + base.model.get('toggleID'), $.proxy(base.getStatus, base));
			Adapt.on('annotationsDisplayControls::hide-' + base.model.get('_id'), $.proxy(base.hideControls, base));
		},

		reportDisplayControlRendered: function() {
			Adapt.trigger('annotationsDisplayControls::rendered-' + this.model.get('_id'));
		},

		getStatus: function() {
			Adapt.trigger('annotationsDisplayControls::status-' + this.model.get('toggleID'), this.displayed);
		},

		setupAttributes: function() {
			this.block_id = this.model.get('_id');
			this.dom = this.getDomRefsObject();
		},

		getDomRefsObject: function() {
			return {
				showAnnotationsButton: false,
				hideAnnotationsButton: false
			};
		},

		saveDOMRefs: function() {
			this.dom.showAnnotationsButton = this.$('.annotations-show');
			this.dom.hideAnnotationsButton = this.$('.annotations-hide');
		},

		renderTemplate: function() {
			var template = Handlebars.templates['annotationsDisplayControls'];

			this.$el.html(template({
				id: this.block_id
			})).appendTo($('.' + this.block_id));
		},

		toggleAnnotationDisplayButton: function() {
			this.displayed = !this.displayed;
			this.dom.showAnnotationsButton.toggle();
			this.dom.hideAnnotationsButton.toggle();
			Adapt.trigger('annotations::toggle-' + this.model.get('toggleID'));
		},

		hideControls: function() {
			this.dom.showAnnotationsButton.hide();
			this.dom.hideAnnotationsButton.hide();
		},
	});

	return AnnotationsControls;

});

