Modernizr.load ([{
    test: true,
    yep: 'assets/ckeditor.js'
}]);

define('extensions/adapt-cleoBlockBullets/js/adapt-cleoBlockBullets',['require','coreJS/adapt','./utils/utils'],function(require) {

    var Adapt = require('coreJS/adapt');
    var Utils = require('./utils/utils');

	Adapt.on('pageView:ready', function(view) {

		var extensionData = Utils.getGlobalParams('_cleoBlockBullets');
		if ( extensionData && Utils.checkIsEnable('_cleoBlockBullets') ) {

			Adapt.on('blockBullets::save', function(data) {
            	sendDataToCleo(extensionData.url, prepareBulletsData(data, extensionData));
          	});
		}
	});

	function prepareBulletsData(data, extensionData) {
		return {
      		_cleoBlockId: data._cleoBlockId || 0,
      		current_user_id: 		extensionData.current_user_id,
      		current_user_name: 		extensionData.current_user_name,
      		heightForBulletText: 	data.heightForBulletText || undefined,
      		widthForBulletText: 	data.widthForBulletText || undefined,
      		positionOfBulletText: 	{
				top: 	parseInt(data.positionOfBulletText.top, 10),
				left: 	parseInt(data.positionOfBulletText.left, 10)
			}

      	};
	};

	function sendDataToCleo(url, data) {
		$.ajax({
			type: 'POST',
			url: url,
			data: data,
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
			}
		}).success(function(response) {
			if(response.success) {

			} else{
				alert("Error: Your changes didn't get saved!");
			}
		}).error(function(e) {
			alert("Error: Your changes didn't get saved!");
			console.log(e);
		});
	};
});

