define('extensions/adapt-validatorComments/js/adapt-validatorCommentsList/js/adapt-validatorCommentsListView',['require','underscore','coreJS/adapt','backbone','jquery','./adapt-validatorCommentView','./adapt-validatorCommentModel','./adapt-validatorCommentsCollection'],function(require) {


	var _ = require('underscore');
	var Adapt = require('coreJS/adapt');
	var Backbone = require('backbone');
	var $ = require('jquery');
	var CommentView = require('./adapt-validatorCommentView');
	var Comment = require('./adapt-validatorCommentModel');
	var Comments = require('./adapt-validatorCommentsCollection');
	var CommentsListView = Backbone.View.extend({

		events: {
			"click .validator-comments-list-extension-button": "onSpecButtonClicked",
		},
		className: 'validator-comments-list-extension',
		initialStateChanged: false,
		commentsList: undefined,
		edition: false,
		newComment: false,
		newCommentControlsActivationStatus: true,
		commentsFeed: false,
		block_id: "",
		commentsListContainer: false,
		toggleButton: false,
		extensionWidget: false,
		params: {
			is_enabled: true,
			editable: true,
			current_user_id: "",
			current_user_name: "",
			togglable: true,
			draggable: true,
			openButtonText: "open comments list button",
			closeButtonText: "close comments list button"
		},


// ************************* VIEW LOGIC **************************************//

		initialize: function() {
			this.block_id = this.model.get('block_id');
			this.params = Object.assign({}, this.params, this.model.get('_commentsList'), Adapt.course.get('_globals')._extensions._validatorComments);

			if (this.params.is_enabled) {
				if (this.params._comments.length > 0) {
					this.commentsList = new Comments(this.params._comments);
				} else {
					this.commentsList = new Comments();
				}

				this.setupEvents();
				this.registerToExternalListeners();
				this.render();
			}
		},

		render: function() {

			if (this.params.is_enabled) {
				var template = Handlebars.templates['validatorCommentsList'];

				var position = this.model.get('position');

				var viewParams = {
					block_id: this.block_id,
					editable: this.params.editable,
					togglable: this.params.togglable,
					emptyCollection: (this.commentsList.size() <= 0),
					positionSet: false
				};

				if (position.top !== 0 && position.left !== 0) {
					viewParams['top'] = position.top;
					viewParams['left'] = position.left;
					viewParams['positionSet'] = true;
				}

				this.$el.html(template(viewParams)).appendTo($('.' + this.model.get('block_id')));

				this.newComment = this.$('#validator-comments-list-comment-textarea-'+this.block_id);
				this.commentsFeed = this.$('#validator-comments-list-extension-widget-feed-'+this.block_id);
				this.commentsListContainer = this.$('.validator-comments-list-extension-widget');
				this.toggleButton = this.$('.validator-comments-list-extension-button');
				this.extensionWidget = this.$(".validator-comments-list-extension-widget");

				this.addAllCommentsToView();

				_.defer(_.bind(this.postRender, this));
			}

			return this;
		},

		postRender: function() {
			var base = this;

			this._setupSaveOnEnterKeyPress();
			this.setLayout();
			this.listenTo(Adapt, 'remove', this.remove);
			this.listenTo(Adapt, 'validator-comments-list-extension-widget:open', this.checkIfShouldClose);

			if (this.params.draggable) {
				this.commentsListContainer.draggable({
					addClasses: false,
					containment: $('.'+this.block_id),
					stop: function( event, ui ) {
						base.model.set({'position': ui.position}, {silent: true});
						Adapt.trigger('adapt::validatorComments/reportDrag-' + base.block_id, base.model);
					}
				});
			}

		},

		destroyView: function() {
			this.undelegateEvents();
			this.$el.removeData().unbind();
			this.remove();
			Backbone.View.prototype.remove.call(this);
		},

		addCommentToView: function(comment) {
			var base = this;

			comment.attributes.block_id = this.block_id;
			comment.attributes.current_user_id = this.params.current_user_id;
			comment.attributes.editable = this.params.editable;
			comment.attributes.togglable = this.params.togglable;

			var commentView = new CommentView({ model: comment});

			this.listenTo(
				commentView,
				'commentsList' + this.block_id + ':edit',
				$.proxy(base.editComment, base)
			);
			this.listenTo(
				commentView,
				'commentsList' + this.block_id + ':delete',
				$.proxy(base.requestCommentDeletion, base)
			);
			this.listenTo(
				commentView,
				'commentsList' + this.block_id + ':cancelDeletion',
				$.proxy(base.cancelDeletionComment, base)
			);

			this.commentsFeed.prepend(commentView.render().el);
		},

		addAllCommentsToView: function() {
			this.commentsFeed.empty();
			this.commentsList.sort();
			this.commentsList.each(this.addCommentToView, this);
		},

		activateNewComment: function() {
			if (!this.newCommentControlsActivationStatus) {
				this.$('#validator-comments-list-extension-button-cancel-'+this.block_id)
					.fadeTo("fast", 1);
				this.$('#validator-comments-list-extension-button-save-'+this.block_id)
					.fadeTo("fast", 1);
				this.newCommentControlsActivationStatus = true;
			}
		},

		disableNewComment: function() {
			if (this.newCommentControlsActivationStatus) {
				this.$('#validator-comments-list-extension-button-cancel-'+this.block_id)
					.fadeTo("fast", 0.3);
				this.$('#validator-comments-list-extension-button-save-'+this.block_id)
					.fadeTo("fast", 0.3);
				this.newCommentControlsActivationStatus = false;
			}
		},

		_setupSaveOnEnterKeyPress: function() {
			var base = this;

			this.newComment.keyup(function(event){
				if (
					event.keyCode === 13 &&
					!event.shiftKey
				){
					event.preventDefault();
					base.addNewComment();
				}
			});
		},

// ************************* DOMAIN LOGIC ************************************//

		cancelNewComment: function() {
			if (this.newCommentControlsActivationStatus) {
				this.newComment.val('');
				if (this.commentsList.size() <= 0) {
					Adapt.trigger('adapt::validatorComments/reportDesctruction-' + this.block_id, this.model);
					this.destroyView();
				}
			}
		},

		addNewComment: function() {
			var messageContent = this._getCommentContent();

			if (messageContent !== "" && this.newCommentControlsActivationStatus) {
				var newCommentData = new Comment({
					"id": this._GUID(),
					"block_id": this.block_id,
					"author_id": this.params.current_user_id,
					"author_name": this.params.current_user_name,
					"comment": messageContent,
					"date": "",
					"timestamp": new Date().getTime(),
					"editable": true,
					"deletion_status": false
				});

				this.commentsList.add(newCommentData);
				this.cancelNewComment();
				this.commentsFeed.animate({ scrollTop: 0 }, "fast");
				this._reportExtensionStateChanged();
			}
		},

		editComment: function(_comment) {
			if (this._commentBelongsToUser(_comment)) {
				var comment = this.commentsList.get(_comment.id);

				comment.set({
					'comment': _comment.comment,
					'date': _comment.date,
					'timestamp': _comment.timestamp,
					'changed': _comment.changed,
				});
				this.commentsList.set(comment, { remove: false });
				this.commentsList.trigger('reset');
				this.reportToValidatorComments();
				this._reportExtensionStateChanged();
			}
		},

		deleteComment: function(_comment) {
			if (this._commentBelongsToUser(_comment)) {
				var comment = this.commentsList.get(_comment.id);
				if (comment) {
					this.activateNewComment();
					this.commentsList.remove(comment);
					this.commentsList.trigger('reset');
					this._reportExtensionStateChanged();
				}
			}
		},

		_commentBelongsToUser: function(comment) {
			return comment.author_id === this.params.current_user_id;
		},

		validateDeletionRequest: function(data) {
			return (
				data &&
				typeof data === "object" &&
				data.hasOwnProperty('id') &&
				data.id === this.id
			);
		},

// ************************* EVENTS ******************************************//

		setupEvents: function() {
			this.setupDelegetedEvents();
			this.setupExternalEvents();
			this.setupLocalEvents();
		},

		setupLocalEvents: function() {
			var base = this;

			this.listenTo(this.commentsList, 'add', this.addCommentToView);
			this.listenTo(this.commentsList, 'reset', this.addAllCommentsToView);
			this.listenTo(this.commentsList, 'update', this.reportToValidatorComments);
			this.listenTo(Adapt, 'commentsList::' + this.block_id + '/activate-new-comment', this.activateNewComment);
			this.listenTo(Adapt, 'commentsList::' + this.block_id + '/disable-new-comment', this.disableNewComment);

			Adapt.on('adapt::validatorComments/requestDesctructionOffAllDisplayedStickNotes-' + this.block_id, function() {
				base.destroyView();
			});

			Adapt.on('adapt::validatorComments/toggle', function() {

				if (base.extensionWidget.is(':visible')) {
					base.toggleButton.show().offset(
							base.extensionWidget.offset()
					);
					base.extensionWidget.hide().removeClass('widget-open');
				}


				if (!base.newCommentControlsActivationStatus) {
					Adapt.trigger('commentsList::' + base.block_id + '/activate-new-comment');
				}
			});
		},

		reportToValidatorComments: function() {
			var base = this;

			Adapt.trigger('adapt::validatorComments/reportUpdate-' + this.block_id, function() {
				var currentCommentsList = base.model.get('_commentsList');

				currentCommentsList._comments = base.commentsList.toJSON();

				base.model.set('_commentsList', currentCommentsList);

				return base.model;
			}());
		},

		setupDelegetedEvents: function() {

			var customEventsListener = {};

			customEventsListener[
				'click #validator-comments-list-extension-button-cancel-' +
				this.block_id
			] = 'cancelNewComment';

			customEventsListener[
				'click #validator-comments-list-extension-button-save-' +
				this.block_id
			] = 'addNewComment';

			this.delegateEvents(
				_.extend(
					_.clone(this.events),
					customEventsListener
				)
			);
		},

		registerToExternalListeners: function() {
			var base = this;

			$(window.document).trigger('adapt::register', [{
				"extension-name": 'validatorCommentsList',
				"extension-id": base.block_id,
			}]);
		},

		setupExternalEvents: function() {
			var base = this;

			$(window.document).on(
				'adapt::query/request-state',
				function() {
					$(window.document).trigger(
						'adapt::query/response', [{
							"extension-name": 'validatorCommentsList',
							"extension-id": base.block_id,
							response: {
								block_id: base.block_id,
								state: base.initialStateChanged
							}
						}]);
				}
			);

			$(window.document).on(
				'adapt::query/request-collections',
				function() {
					$(window.document).trigger(
						'adapt::query/response', [{
							"extension-name": 'validatorCommentsList',
							"extension-id": base.block_id,
							response: {
								block_id: base.block_id,
								collection: base.commentsList.toJSON()
							}
						}]);
				}
			);

			$(window.document).on(
				'adapt::query/request-registration',
				function() {
					base.registerToExternalListeners();
				}
			);

			$(window.document).on(
				'adapt::query/request-deletion',
				function(event, data) {
					if (base._validateEventData(data)) {
						base.deleteComment(data.payload);
					}
				}
			);

		},

		requestCommentDeletion: function(_comment) {
			$(window.document).trigger('adapt::query/deletion-approval-request', [{
				target: 'commentsList-' + this.block_id,
				payload: _comment
			}]);
		},

		_reportExtensionStateChanged: function() {
			this.initialStateChanged = true;
			$(window.document).trigger('adapt::query/state-changed');
		},

		_eventNotRegistered: function(eventName) {
			return !$._data(window.document,'events')
					.hasOwnProperty('adapt::comments-list/'+eventName);
		},

		_validateEventData: function(data) {
			return	data &&
							typeof data === "object" &&
							data.hasOwnProperty('target') &&
							data.hasOwnProperty('payload') &&
							data.target === "commentsList-" + this.block_id &&
							typeof data.payload === 'object' &&
							data.payload.hasOwnProperty('id')  &&
							data.payload.hasOwnProperty('author_id')  &&
							data.payload.author_id === this.params.current_user_id;
		},

// ************************* Adapt-hint updated code *************************//

		setLayout: function() {
			if (Adapt.config.get('_defaultDirection') == 'rtl' && Adapt.device.screenSize === 'small' ) {
      	$('.' + this.model.get('block_id') + " .component-title-inner").css({
					paddingLeft: '35px'
				});
    	} else {
        $('.' + this.model.get('block_id') + " .component-title-inner").css({
					paddingRight: '35px'
				});
      }

			this.commentsListContainer.show();
			this.toggleButton.hide();
		},

		onSpecButtonClicked: function(event, preventEvent) {

			preventEvent = preventEvent || true;
			if (event && preventEvent) event.preventDefault();

			var $specDetail = this.commentsListContainer;
			var closeAria = this.params.closeButtonText;
			var openAria = this.params.openButtonText;

			if (!$specDetail.hasClass('widget-open')) {

				$(event.currentTarget).attr({
					'aria-label': closeAria
				});
				$specDetail.velocity({
					scaleX: 1,
					scaleY: 1
				}, {
					duration: 800,
					display: 'block',
					easing: [500, 35]
				});
				$specDetail.addClass('widget-open');

				this.toggleButton.hide();
				$specDetail.a11y_focus();
				Adapt.trigger('validator-comments-list-extension-widget:open', this.model.get('block_id'));

			} else {
				$(event.currentTarget).attr({
					'aria-label': openAria
				});
				$specDetail.hide();
				$specDetail.removeClass('widget-open');

				this.toggleButton.show();
			}
		},

		checkIfShouldClose: function(id) {
			if (this.model.get('block_id') !== id) {
				var $widget = $('.' + this.model.get('block_id') + " .validator-comments-list-extension-widget");
				var $button = $('.' + this.model.get('block_id') + " .validator-comments-list-extension-button");

				$widget.velocity({
					scaleX: 0,
					scaleY: 0
				}, {
					duration: 300,
					display: 'none'
				});

				$widget.removeClass('widget-open');
				$button.show();
			}
		},

// ************************* HELPERS *****************************************//

		_GUID: function() {
			//http://stackoverflow.com/a/2117523
			return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(
				/[xy]/g,
				function(c) {
					var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
					return v.toString(16);
				}
			);
		},

		// http://stackoverflow.com/a/6234804
		_escapeHtml: function(unsafe) {
			return unsafe
				.replace(/&/g, "&amp;")
				.replace(/</g, "&lt;")
				.replace(/>/g, "&gt;")
				.replace(/"/g, "&quot;")
				.replace(/'/g, "&#039;");
		},

		_getCommentContent: function() {
			return this._escapeHtml(this.newComment.val()).replace(/\n/g, '<br/>');
		},

	});


	return CommentsListView;

});

