define('extensions/adapt-commentsList/js/adapt-commentsList',['require','coreJS/adapt','./adapt-commentsList-validation','./adapt-commentsListView'],function(require) {

	var Adapt = require('coreJS/adapt');
	var Validation = require('./adapt-commentsList-validation');
	var CommentsListExtensionView = require('./adapt-commentsListView')

	Adapt.on('blockView:postRender', function(view) {
		var extension = Adapt.course.get('_globals')._extensions;

		if (!Validation.isCommentsListActive(extension)){
			return;
		}

		if (Validation.checkRequiredConfig(view)) {
			if (Validation.shouldInstantiate(
					view.model.get('_commentsList'),
					extension._commentsList.editable
			)) {
				new CommentsListExtensionView({
					model: view.model
				});
			}
		}
	});
});

