define('extensions/adapt-reviewText/js/adapt-reviewTextsView',['require','jquery','backbone','coreJS/adapt','./adapt-reviewTextView','./adapt-reviewTextModel','./adapt-reviewTextCollection'],function(require) {

	var $ = require('jquery');
	var Backbone = require('backbone');
	var Adapt = require('coreJS/adapt');
	var reviewTextView = require('./adapt-reviewTextView');
	var ReviewTextModel = require('./adapt-reviewTextModel');
	var ReviewTextCollection = require('./adapt-reviewTextCollection');

  var ReviewTextsView = Backbone.View.extend({

			events: {},
      className: "review-text-extension",

      componentView: false,
      data: false,
      that: false,
      reviewTextCollection: [],
      initialStateChanged: false,

      initialize: function () {
          this.setupAttributes();
          this.setupCollection();
          this.setupEvents();
          this.render();
          this.registerToExternalListeners();
      },

      setupAttributes: function() {
          this.componentView = this.model._componentView;
          this.data = this.model.toJSON();
      },

      setupCollection: function() {
          this.reviewTextCollection = new ReviewTextCollection();
      },

      setupEvents: function() {
          this.setupExternalEvents();
          this.setupLocalEvents();
      },

      setupLocalEvents: function() {
          this.setupCollectionEvents()
              .setupAdaptEvents();
      },

      setupCollectionEvents: function() {
          this.listenTo(this.reviewTextCollection, 'add', this.initializeReviewTextView);

          return this;
      },

      setupAdaptEvents: function() {
          var base = this;

          Adapt.on('reviewText::textChanged-' + this.data._id, function() {
             base._reportExtensionStateChanged();
          });

          return this;
      },

      generateResponseObject: function() {
          return {
              "extension-name": 'reviewText',
              "extension-id": this.data._id,
              response: {
                  component_id: this.data._id
              }
          };
      },

      getResponseObject: function(type) {
          var responseObject = this.generateResponseObject();
          switch(type) {
              case 'ExtensionState':
                  responseObject.response['state'] = this.initialStateChanged;
              break;
              case 'Collection':
                  responseObject.response['collection'] = this.reviewTextCollection.toJSON();
              break;
          }
          return responseObject;
      },

      setupExternalEvents: function() {
          this.listenForExternalExtensionStateRequest();
          this.listenForExternalCollectionRequest();
          this.listenForExternalRegistrationRequest();
      },

      listenForExternalExtensionStateRequest: function() {
          var base = this;

          $(window.document).on('adapt::query/request-state', function() {
              $(window.document).trigger(
                  'adapt::query/response',
                  [base.getResponseObject('ExtensionState')]
              );
          });
      },

      listenForExternalCollectionRequest: function() {
          var base = this;

          $(window.document).on('adapt::query/request-collections', function() {
                  $(window.document).trigger(
                      'adapt::query/response',
                      [base.getResponseObject('Collection')]
                  );
          });
      },

      listenForExternalRegistrationRequest: function() {
          var base = this;

          $(window.document).on('adapt::query/request-registration', function() {
                  base.registerToExternalListeners();
          });
      },

      registerToExternalListeners: function() {
          var base = this;

          $(window.document).trigger('adapt::register', [{
              "extension-name": 'reviewText',
              "extension-id": base.data._id,
          }]);
      },

      _reportExtensionStateChanged: function() {
          this.initialStateChanged = true;
          $(window.document).trigger('adapt::query/state-changed');
      },

      initializeReviewTextView: function(reviewText) {
          new reviewTextView({ model: reviewText });
      },

      render: function () {
          _.defer(_.bind(function() { this.postRender(); }, this));
          return this;
      },

      getAllReviewableText: function(){
          var that = this;
          if(this.data._type === 'component') {
            if(this.data._component === 'text' || this.data._component === 'accordionWithAudio') {

              $(this.componentView).find('.'+this.data._component+'-body-inner').each(function(index){
                  that.generateReviewTextModel('body', index, this);
              });

              $(this.componentView).find('.'+this.data._component+'-item-body-inner').each(function(index){
                  that.generateReviewTextModel('item', index, this);
              });

              $(this.componentView).find('.wcn-bodyText .title-inner').each(function(index){
                  that.generateReviewTextModel('wcn-item', index, this);
              });
            }
          } else  if(this.data._type === 'block') {
            $(this.componentView).find(".block-content-items-bullets").off("mousedown");
            $(this.componentView).find('.block-content-items-bullets .bullet-text-inner').each(function(index){
                that.generateReviewTextModel('block-item', index, this);
            });
          }
      },

      generateReviewTextModel: function(type, index, domElement) {
          type = type || 'body';

          this.reviewTextCollection.add(
              new ReviewTextModel({
                  _component: this.data._component,
                  _componentId: this.data._id,
                  _view: domElement,
                  text: '',
                  index: index,
                  _type: type
              })
          );
      },

      postRender: function() {
          this.getAllReviewableText();
      },
  });

  // Return ReviewTextsView so it can be required
  return ReviewTextsView;

});

