define('extensions/adapt-commentsList/js/adapt-commentsList-validation',[],function() {

	var Validation = {
		isCommentsListActive: function(extensions) {
			return extensions &&
				typeof extensions === "object" &&
				extensions.hasOwnProperty('_commentsList') &&
				typeof extensions._commentsList === "object" &&
				extensions._commentsList.hasOwnProperty('editable') &&
				extensions._commentsList.hasOwnProperty('is_enabled') &&
				extensions._commentsList.is_enabled;
		},
		checkRequiredConfig: function(view) {
			return view.model.has('_commentsList') &&
				view.model.get('_commentsList').hasOwnProperty('_comments');
		},
		shouldInstantiate: function(commentsList, editable) {
			return (
				(!editable && commentsList._comments.length > 0)
				||
				editable
			);
		}
	};

	return Validation;
});

