define('extensions/adapt-validatorComments/js/adapt-validatorCommentsView',['require','coreJS/adapt','backbone','jquery','./adapt-validatorCommentsList/js/adapt-validatorCommentsListView','./adapt-validatorComments-StickyModel','./adapt-validatorComments-StickyCollection'],function(require) {

	var Adapt = require('coreJS/adapt');
	var Backbone = require('backbone');
	var $ = require('jquery');
	var CommentsListView = require('./adapt-validatorCommentsList/js/adapt-validatorCommentsListView');
	var StickyNote = require('./adapt-validatorComments-StickyModel');
	var StickyNotes = require('./adapt-validatorComments-StickyCollection')

	var ValidatorCommentsView = Backbone.View.extend({
		params: {},
		block_id: undefined,
		stickyNotes: [],
		initialStateChanged: false,
		renderParams: false,

		initialize: function() {
			this.setupAttributes();

			if (this.params.is_enabled) {
				this.setupCollection();
				this.setupEvents();
				this.render();
			}
		},

// ************************* VIEW LOGIC **************************************//

		render: function() {
			this.renderTemplate();
			_.defer(_.bind(this.postRender, this));
			this.addAllStickyNotesToView();

			return this;
		},

		postRender: function() {

		},

		setupAttributes: function() {
			this.data = this.model.toJSON();
			this.block_id = this.model.get('_id');
			this.params = Adapt.course.get('_globals')._extensions._validatorComments;
		},

		setupCollection: function() {
			if (this.data.validatorComments.stickyNotes.length > 0) {
				this.stickyNotes = new StickyNotes(this.data.validatorComments.stickyNotes);
			} else {
				this.stickyNotes = new StickyNotes();
			}
		},

		renderTemplate: function() {
			var template = Handlebars.templates['validatorComments'];

			this.$el.html(
				template(
					this.getRenderParams()
				)
			).appendTo(
				$('.' + this.model.get('_id'))
			);
		},

		getRenderParams: function() {
			return {
				id: this.block_id,
				editable: this.params.editable
			};
		},

		reportExtensionStateChanged: function() {
			this.initialStateChanged = true;
			$(window.document).trigger('adapt::query/state-changed');
		},

		setupEvents: function() {
			this.setupDelegetedEvents();
			this.setupLocalEvents();
			this.setupExternalEventsListener();
			this.sendRegistrationRequestToExternalModules();
		},

		setupLocalEvents: function() {
			var base = this;

			this.listenTo(this.stickyNotes, 'add', this.addStickyNoteToView);
			this.listenTo(this.stickyNotes, 'reset', this.addAllStickyNotesToView);
			this.listenTo(Adapt, 'remove', this.remove);
			this.listenTo(this.model, 'change', this.render);

			Adapt.on('adapt::validatorComments/reportDesctruction-' + this.block_id, function(data) {
				base.stickyNotes.remove(data);
				base.reportExtensionStateChanged();
			});

			Adapt.on('adapt::validatorComments/reportDrag-' + this.block_id, function(data) {
				base.stickyNotes.set(data, { remove: false });
				base.reportExtensionStateChanged();
			});

			Adapt.on('adapt::validatorComments/reportUpdate-' + this.block_id, function(data) {
				base.stickyNotes.set(data, { remove: false });
				base.reportExtensionStateChanged();
			});
		},

		setupDelegetedEvents: function() {
			var customEventsListener = {};

			customEventsListener[
				'click #validator-comments-extension-new-sticky-' +
				this.block_id
			] = 'addNewStiky';

			this.delegateEvents(
				customEventsListener
			);
		},

		setupExternalEventsListener: function() {
			var base = this;

			$(window.document).on(
				'adapt::query/request-state',
				function() {
					$(window.document).trigger(
						'adapt::query/response', [{
							"extension-name": 'validatorStickyNotes',
							"extension-id": base.block_id,
							response: {
								block_id: base.block_id,
								state: base.initialStateChanged
							}
						}]);
				}
			);

			$(window.document).on(
				'adapt::query/request-collections',
				function() {
					$(window.document).trigger(
						'adapt::query/response', [{
							"extension-name": 'validatorStickyNotes',
							"extension-id": base.block_id,
							response: {
								block_id: base.block_id,
								collection: base.stickyNotes.toJSON()
							}
						}]);
				}
			);

			$(window.document).on(
				'adapt::query/request-registration',
				function() {
					base.sendRegistrationRequestToExternalModules();
				}
			);
		},

		sendRegistrationRequestToExternalModules: function() {
			var base = this;

			$(window.document).trigger('adapt::register', [{
				"extension-name": 'validatorStickyNotes',
				"extension-id": base.block_id,
			}]);
		},

		addStickyNoteToView: function(stickyNote) {

			stickyNote.attributes.block_id = this.block_id;
			stickyNote.attributes.current_user_id = this.params.current_user_id;

			new CommentsListView({ model: stickyNote});
		},

		addAllStickyNotesToView: function() {
			this.requestDesctructionOffAllDisplayedStickNotes();
			this.stickyNotes.each(this.addStickyNoteToView, this);
		},

		requestDesctructionOffAllDisplayedStickNotes: function() {
			Adapt.trigger('adapt::validatorComments/requestDesctructionOffAllDisplayedStickNotes-' + this.block_id);
		},

		addNewStiky: function() {
			this.stickyNotes.add(
				new StickyNote({
					id: this._GUID(),
					block_id: this.block_id,
					_commentsList: {
						current_user_id: this.params.current_user_id,
						current_user_name: this.params.current_user_name,
						_comments: []
					}
				})
			);
			this.reportExtensionStateChanged();
		},

		_GUID: function() {
			//http://stackoverflow.com/a/2117523
			return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(
				/[xy]/g,
				function(c) {
					var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
					return v.toString(16);
				}
			);
		},
	});

	return ValidatorCommentsView;
});

