define('extensions/adapt-spaceBarEnabler/js/adapt-spaceBarEnabler',['require','coreJS/adapt','./utils/utils'],function(require) {

    var Adapt = require('coreJS/adapt');
    var Utils = require('./utils/utils');

	// view argument represent the block view
	Adapt.on('app:dataReady', function(view) {
		var extensionData = Utils.getGlobalParams('_spaceBarEnabler');
		if ( extensionData &&
			 extensionData.hasOwnProperty('_isEnabled') &&
			 extensionData._isEnabled
		) {
			$('body').on('keydown', function() {
				$('body').off('keydown');
			});	
		}
	});
});

