define('extensions/adapt-validatorComments/js/adapt-validatorComments-StickyModel',['require','backbone'],function(require) {
	var Backbone = require('backbone');

	var StickyNoteModel = Backbone.Model.extend({
		defaults: {
			_id: "",
			position: {
				top: 0,
				left: 0
			},
			_commentsList: {
				is_enabled: true,
				editable: true,
				current_user_id: "",
				current_user_name: "",
				togglable: true,
				draggable: true,
				openButtonText: "open comments list button",
				closeButtonText: "close comments list button",
				_comments: []
			},
			timestamp: new Date().getTime()
		}
	});

	return StickyNoteModel;
});

