define('extensions/adapt-annotation/js/adapt-annotation/annotationView',['require','jquery','coreJS/adapt','backbone','../utils/utils'],function(require) {

	var $ = require('jquery');
	var Adapt = require('coreJS/adapt');
	var Backbone = require('backbone');
	var Utils = require('../utils/utils');

	var AnnotationView = Backbone.View.extend({
		events: {
			'click .annotation-editor-button-cancel': 'cancelEdition',
			'click .annotation-editor-button-save': 'saveComment',
			'click .annotation-comment-controls-edit': 'editAnnotation',
			'click .annotation-comment-controls-delete': 'requestDeletion'
		},

		initialize: function() {
			this.setupAttributes()
					.setupEvents()
					.render();
		},

		render: function() {
			this.renderTemplate();

			if (this.isPreDefined()) {
				this.validateDrawnShape();
			} else {
				this.focusCursor();
			}
			_.defer(_.bind(this.postRender, this));

			return this;
		},

		postRender: function() {
			this.setupDelegatedEvents()
					.setupSaveOnEnterKeyPress();

			if (this.model.get('isPreDefined') && !this.model.get('edition')) {
				this.updateCommentPosition();
				this.hideEditionZone();
			}
		},

		setupSaveOnEnterKeyPress: function() {
			var base = this;

			this.$('#annotation-comment-textarea-' + this.model.get('id'))
					.keydown(function(event) {
						if (event.keyCode === 13 && !event.shiftKey) {
							event.preventDefault();
							base.saveComment();
						}
					});
		},

		setupAttributes: function() {
			this.getGlobalParams()
					.processOwnership()
					.parseDimensions()
					.computeShape()
					.computeInitialDisplay();

			return this;
		},


		getGlobalParams: function() {
			this.global = Utils.getGlobalParams('_annotations');

			return this;
		},

		processOwnership: function() {
			if (this.global.editable) {
				this.model.set('ownAnnotation', this.model.get('author_id') === this.global.current_user_id);
			} else {
				this.model.set('ownAnnotation', false);
			}

			return this;
		},

		computeShape: function() {
			this.shape = Object.assign({}, this.getShape(), this.model.get('coordinates'), {
				width: this.model.get('width'),
				height: this.model.get('height')
			});

			return this;
		},

		computeInitialDisplay: function() {
			this.model.set({
				isPreDefined: this.isPreDefined(),
				edition: !this.isPreDefined()
			});

			return this;
		},

		getCommentContent: function(editMode) {

			editMode = editMode || false;

			return (editMode) ?
				Utils.revertHtmlChars(this.model.get('comment')).replace(/<br\s*\/?>/g, '\n') :
				Utils.escapeHtml($('#annotation-comment-textarea-'+this.model.get('id')).val()).replace(/\n/g, '<br/>');
		},

		parseDimensions: function() {
			this.model.set({
				width: parseInt(this.model.get('width'), 10),
				height: parseInt(this.model.get('height'), 10),
			});

			return this;
		},

		showEditionZone: function() {
			this.$('.annotation-comment-container').show();

			return this;
		},

		hideEditionZone: function() {
			this.$('.annotation-comment-container').hide();
		},

		showCommentContainer: function() {
			if (!this.model.get('edition')) {
				this.showEditionZone();
			}
		},

		hideCommentContainer: function() {
			if (!this.model.get('edition')) {
				this.hideEditionZone();
			}
		},

		updateCommentPosition: function() {
			var offset = this.$('.highlight').offset();
			offset.top = offset.top + this.$('.highlight').height() + 2;
			this.$('.annotation-comment-container').offset(offset);

			return this;
		},

		renderTemplate: function() {
			var template = Handlebars.templates['annotation'];

			this.$el.html(template(this.model.toJSON()));

			return this;
		},

		setupEvents: function() {
			this.setupLocalEvents()
			this.setupDelegatedEvents();

			return this;
		},

		setupLocalEvents: function() {
			this.setupAdaptEvents();
			this.setupDrawingEvents();
		},

		setupDrawingEvents: function() {
			var base = this;
			$('body')
				.on('mouseup', $.proxy(base.validateDrawnShape, base))
				.on('mousemove', $.proxy(base.handleDragging, base));
		},

		setupAdaptEvents: function() {
			var base = this;

			Adapt.on('annotations::requestDestructionOfAllViews-' + this.model.get('component_id'), function(){
				base.destroyAnnotation();
			});
			Adapt.on('annotations::requestDestruction-' + this.model.get('id'), function(){
				base.destroyAnnotation();
			});
			Adapt.on('annotations::toggle-' + this.model.get('component_id'), function(){
				base.$('.annotation-context').toggle();
			});
			var trigger = this.model.get('trigger');
			if (trigger) {
				Adapt.on('annotations::toggle-' + this.model.get('component_id') + '-trigger-' + trigger, function(){
					base.$('.annotation-context').toggle();
				});
			}
		},

		setupDelegatedEvents: function() {
			var delegatedEvents = {
				'mouseenter .highlight': 'showCommentContainer',
				'mouseenter .annotation-comment-container': 'showCommentContainer',
				'mouseleave .highlight': 'hideCommentContainer',
				'mouseleave .annotation-comment-container': 'hideCommentContainer'
			};

			if (this.global.editable) {
				delegatedEvents['dblclick .annotation-comment'] = 'editAnnotation';
			}

			this.delegateEvents(_.extend(_.clone(this.events), delegatedEvents));

			return this;
		},

		isPreDefined: function() {
			return this.model.get('width') !== 0 &&
				this.model.get('height') !== 0;
		},

		getShape: function() {
			return {
				top: 0,
				left: 0,
				width: 0,
				height: 0
			};
		},

		handleDragging: function(event) {
			var containerCoordinates = this.model.get('drawingContainerCoordinates');
			var shapeDimensions = {
				width: event.pageX - this.shape.left - containerCoordinates.left,
				height: event.pageY - this.shape.top - containerCoordinates.top
			};

			if (!this.isPreDefined()) {
				var drawingContainer = this.model.get('drawingContainer');
				var maxWidth =  containerCoordinates.left + drawingContainer.width();
				var maxHeight =  containerCoordinates.top + drawingContainer.height();

				if (
					drawingContainer &&
					maxWidth - event.pageX >= 0 &&
					maxHeight - event.pageY >= 0
				) {
					this.shape = Object.assign({}, this.shape, shapeDimensions);
					this.updateAnnotationDisplay();
				}
			} else {
				this.shape = Object.assign({}, this.shape, shapeDimensions);
				this.updateAnnotationDisplay();
			}
		},

		updateAnnotationDisplay: function() {
			this.$('.highlight').css(this.shape);
		},

		validateDrawnShape: function() {
			this.parseDimensions();
			if (this.isAnnotationSizeInvalid()) {
				this.destroyAnnotation();
			}

			this.showEditionZone();
			this.updateCommentPosition();
			this.unregisterRelatedEventsFromBody();
		},

		isAnnotationSizeInvalid: function() {
			return this.shape.width === undefined ||
							this.shape.height === undefined ||
							this.shape.width < 10 ||
							this.shape.height < 10;
		},

		destroyAnnotation: function() {
			this.undelegateEvents();
			this.$el.removeData().unbind();
			this.remove();
			Backbone.View.prototype.remove.call(this);
		},

		unregisterRelatedEventsFromBody: function() {
			$('body').off('mousemove', this.handleDragging)
				.off('mouseup', this.validateDrawnShape);
		},

		cancelEdition: function() {
			if (this.isPreDefined()) {
				this.updateModelInfo(false, false);
				this.render();
			} else {
				this.destroyAnnotation();
			}
		},

		saveComment: function() {
			this.updateModelInfo();
			this.reportExtensionStateChanged();
			this.render();
		},

		editAnnotation: function() {
			this.updateModelInfo(true);
			this.render()
					.showEditionZone()
					.focusCursor();
		},

		reportExtensionStateChanged: function() {
			Adapt.trigger('annotations::state-changed-' + this.model.get('component_id'));

			return this;
		},

		updateModelInfo: function(edition, _changed) {
			var commentContent = this.getCommentContent(edition);
			var changed = _changed || this.model.get('isPreDefined') &&
				commentContent !== this.model.get('comment');

			if (!edition && commentContent === "" || commentContent === "<br/>" ) {
				this.destroyAnnotation();
				return;
			}

			this.model.set({
				height: this.shape.height,
				width: this.shape.width,
				coordinates: {
					top: this.shape.top,
					left: this.shape.left
				},
				isPreDefined: true,
				edition: edition || false,
				comment: commentContent,
				changed: changed,
				timestamp: (_changed || commentContent !== this.model.get('comment')) ?
					new Date().getTime() : this.model.get('timestamp')
			});
		},

		focusCursor: function() {
			$('#annotation-comment-textarea-'+this.model.get('id')).focus();
		},

		requestDeletion: function() {
			Adapt.trigger('annotations::request-deletion-' + this.model.get('component_id'), this.model.toJSON());

			return this;
		},

	});

	return AnnotationView;

});

