define('theme/eom/js/utils/utils',[
    'coreJS/adapt'
], function(Adapt) {

    var Utils = {

        getCurrentPage: function() {
            var currentUrl = window.location.href;
            var temp = currentUrl.split("/");
            var currentPageId = temp[temp.length - 1];
            return currentPageId;
        },

        getArticleModel: function(currentPageId) {
            var articleModel = [];
            var contentObjectModel = Adapt.course.getChildren().models;

            _.each(contentObjectModel, function(item) {
                if (currentPageId == item.get("_id")) {
                    articleModel.push(item.getChildren().models);
                }
            });
            return articleModel;
        },

        getArticles: function() {
            var currentPageId = this.getCurrentPage();
            return Adapt.articles.where({'_parentId': currentPageId});
        },

        getArticleTitle: function() {
            var currentArticleModels = Utils.getArticles();
            if(currentArticleModels) {
                var currentArticleTitle = currentArticleModels[0].get('displayTitle');
                return currentArticleTitle;
            } else {
                return "";
            }
        }

    }
    return Utils;
});
